html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "GT-Walsheim-Pro-Regular", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.layout {
  scrollbar-color: #a8beeb #fff;
}
.layout::-webkit-scrollbar {
  width: 10px;
}
.layout::-webkit-scrollbar-track {
  background: linear-gradient(to bottom, #fff, #a8beeb);
}
.layout::-webkit-scrollbar-thumb {
  background: #7a8bda;
  border-radius: 10px;
}

.layout {
  background: linear-gradient(to bottom, #ffffff, #a8beeb);
  width: 100%;
  height: 100vh;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}

.layout__menu {
  display: flex;
  position: block;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 15vh;
  padding: 5rem 0;
  top: 0;
  left: 0;
  z-index: 100;
  backdrop-filter: blur(5px);
}
.layout__menu .menu__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
}
.layout__menu .menu__navbar .navbar__link-logo {
  margin-left: 2rem;
  font-size: 3.5rem;
}
.layout__menu .menu__navbar .navbar__link-logo a {
  color: #000000;
  display: inline-block;
}
.layout__menu .menu__navbar .navbar__nav {
  width: 50%;
  margin-top: 1rem;
}
.layout__menu .menu__navbar .navbar__nav .nav__list {
  display: flex;
  justify-content: center;
  list-style: none;
  align-items: baseline;
}
.layout__menu .menu__navbar .navbar__nav .nav__list .nav__item .nav__link {
  padding: 1rem 2.5rem;
  color: #000000;
  position: relative;
  display: inline-block;
  font-weight: 600;
  animation: enhance-header linear both;
  animation-timeline: scroll(root block);
  animation-range: 0 250px;
}
.layout__menu .menu__navbar .navbar__nav .nav__list .nav__item .nav__link::before {
  content: "";
  position: absolute;
  bottom: 50%;
  left: 0px;
  width: 100%;
  height: 1px;
  background: #473488;
  display: block;
  -webkit-transform-origin: left top;
  -ms-transform-origin: left top;
  transform-origin: left top;
  -webkit-transform: scale(0, 1);
  -ms-transform: scale(0, 1);
  transform: scale(0, 1);
  -webkit-transition: transform 0.4s cubic-bezier(1, 0, 0, 1);
  transition: transform 0.4s cubic-bezier(1, 0, 0, 1);
}
.layout__menu .menu__navbar .navbar__nav .nav__list .nav__item .nav__link:hover::before {
  -webkit-transform-origin: right top;
  -ms-transform-origin: right top;
  transform-origin: right top;
  -webkit-transform: scale(1, 1.4);
  -ms-transform: scale(1, 1.4);
  transform: scale(1, 1.4);
}

@keyframes topAnimation {
  from {
    transform: translate(0rem, 0);
  }
  to {
    transform: translate(0rem, 3.5rem);
  }
}
@keyframes bottomAnimation {
  from {
    transform: translate(-11.5rem, 0);
  }
  to {
    transform: translate(0rem, 0);
  }
}
.nav__list .nav__link--contact {
  margin-left: 5rem;
  font-weight: 700;
  position: relative;
  letter-spacing: 2px;
  color: #111111;
  padding: 1rem 2.5rem;
  font-size: 2.5rem;
  transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.15s;
}
.nav__list .nav__link--contact::after, .nav__list .nav__link--contact::before {
  border: 0;
  content: "";
  position: absolute;
  height: 40%;
  width: 10%;
  transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  z-index: -2;
  border-radius: 50%;
}
.nav__list .nav__link--contact::before {
  border: 0;
  background-color: #281c4a;
  top: -0.75rem;
  left: 0.5rem;
  animation: topAnimation 2s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.25s infinite alternate;
}
.nav__list .nav__link--contact::after {
  background-color: #473488;
  top: 3.5rem;
  left: 13rem;
  animation: bottomAnimation 2s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.5s infinite alternate;
}
.nav__list .nav__link--contact:hover {
  color: white;
}
.nav__list .nav__link--contact:hover::before, .nav__list .nav__link--contact:hover::after {
  top: 0;
  height: 100%;
  width: 100%;
  border-radius: 0;
  animation: none;
}
.nav__list .nav__link--contact:hover::after {
  left: 0rem;
}
.nav__list .nav__link--contact:hover::before {
  top: 0.5rem;
  left: 0.35rem;
}

.layout__banner {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  height: 50rem;
  width: 100%;
}
.layout__banner .banner__overlay {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 50rem;
  width: 50rem;
}
.layout__banner .banner__overlay .banner__content {
  position: relative;
}
.layout__banner .banner__overlay .banner__content .banner__title {
  display: inline-block;
  font-size: 4rem;
  color: #473488;
  text-wrap: nowrap;
  -webkit-animation: focus-in-contract-bck 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: focus-in-contract-bck 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.layout__banner .banner__overlay .banner__content .banner__subtitle {
  font-size: 2rem;
  color: #000000;
}
.layout__banner .banner__overlay .banner__content .banner__btn {
  display: inline-block;
  color: #ffffff;
  background-color: #473488;
  padding: 1rem 3rem;
  border-radius: 1rem;
  font-size: 1.8rem;
  -webkit-animation: shake-horizontal 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;
  animation: shake-horizontal 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;
  animation-delay: 2s;
  transition: all 0.5s ease-in-out;
}
.layout__banner .banner__overlay .banner__content .banner__btn:hover {
  opacity: 0.8;
}
.layout__banner .banner__video-container {
  width: 80rem;
  height: 50rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.layout__banner .banner__video-container .banner__video {
  position: absolute;
  width: 100%;
  border-radius: 20rem 0 0 20rem;
  -webkit-box-shadow: -16px 22px 71px -30px rgba(0, 0, 0, 0.58);
  -moz-box-shadow: -16px 22px 71px -30px rgba(0, 0, 0, 0.58);
  box-shadow: -16px 22px 71px -30px rgba(0, 0, 0, 0.58);
}

@-webkit-keyframes shake-horizontal {
  0%, 100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  10%, 30%, 50%, 70% {
    -webkit-transform: translateX(-10px);
    transform: translateX(-10px);
  }
  20%, 40%, 60% {
    -webkit-transform: translateX(10px);
    transform: translateX(10px);
  }
  80% {
    -webkit-transform: translateX(8px);
    transform: translateX(8px);
  }
  90% {
    -webkit-transform: translateX(-8px);
    transform: translateX(-8px);
  }
}
@keyframes shake-horizontal {
  0%, 100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  10%, 30%, 50%, 70% {
    -webkit-transform: translateX(-10px);
    transform: translateX(-10px);
  }
  20%, 40%, 60% {
    -webkit-transform: translateX(10px);
    transform: translateX(10px);
  }
  80% {
    -webkit-transform: translateX(8px);
    transform: translateX(8px);
  }
  90% {
    -webkit-transform: translateX(-8px);
    transform: translateX(-8px);
  }
}
@keyframes focus-in-contract-bck {
  0% {
    letter-spacing: 1em;
    -webkit-transform: translateZ(300px);
    transform: translateZ(300px);
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateZ(12px);
    transform: translateZ(12px);
    -webkit-filter: blur(0);
    filter: blur(0);
    opacity: 1;
  }
}
.layout__content {
  position: relative;
  height: 50rem;
  width: 100%;
  content: "hola";
}